
#loading[data-v-6066d6c2]{
	position: fixed;
    top: 0;
    left: -100%;
    z-index: 70;
    width: 100%;
    height: 3px;
    background: #FF4081;
}
#loading.active[data-v-6066d6c2]{
	-webkit-animation: loading-anim 1s ease-in-out infinite;
	animation: loading-anim 1s ease-in-out infinite;
}
#alertBox[data-v-6066d6c2]{
	position: fixed;
	width: 80%;
	margin: 100px 10%;
	z-index: 9999;
	bottom: 0;
}
@-webkit-keyframes loading-anim {
to {
        -webkit-transform: translateX(200%);
        transform: translateX(200%)
}
}
@keyframes loading-anim {
to {
        -webkit-transform: translateX(200%);
        transform: translateX(200%)
}
}
.navbar[data-v-6066d6c2]{
	border-radius: 0;
}
.fade-enter-active[data-v-6066d6c2], .fade-leave-active[data-v-6066d6c2] {
  -webkit-transition: opacity .5s;
  transition: opacity .5s
}
.fade-enter[data-v-6066d6c2], .fade-leave-to[data-v-6066d6c2] /* .fade-leave-active in <2.1.8 */ {
  opacity: 0
}




